import Experience from "components/experience";

function Education({
  children,
  degree,
  endDate,
  fieldOfStudy,
  location,
  school,
  startDate,
}) {
  return (
    <Experience
      companyName={school}
      endDate={endDate}
      location={location}
      startDate={startDate}
      title={degree}
    >
      <small>{fieldOfStudy}</small>
      {children && <div className="mt-3">{children}</div>}
    </Experience>
  );
}

export default Education;
