import { useState } from "react";

import Settings, { languages } from "components/settings";
import GermanResume from "components/resume.de";
import ChineseResume from "components/resume.ch";

import "style/app.scss";

function App() {
  const [lang, setLang] = useState(0);
  const local = languages[lang];

  return (
    <div className="mx-4 my-6 py-6">
      <div className="polka" />
      <Settings lang={lang} setLang={setLang} />
      {local === "de" && <GermanResume />}
      {local === "zh" && <ChineseResume />}
    </div>
  );
}

export default App;
