import { useState } from "react";
import {
  IconSunrise,
  IconSunset,
  IconPrinter,
  IconLanguage,
} from "@tabler/icons";

const fontFamilies = ["sans-serif", "serif", "monospace"];
const fonts = ["Sans Serif", "Serif", "Monospace"];
export const languages = ["de", "zh"];

const style = {
  position: "fixed",
  top: 30,
  right: 30,
};
const className = "mb-4 hide-print";

function Settings({ lang, setLang }) {
  const [lightTheme, setTheme] = useState(true);
  const [fontFamily, setFontFamily] = useState(0);

  const toggleTheme = () => {
    setTheme(!lightTheme);

    if (lightTheme) {
      document.body.classList.add("dark-theme");
    } else {
      document.body.classList.remove("dark-theme");
    }
  };

  const toggleFontFamily = () => {
    const nextFontFamily = (fontFamily + 1) % 3;

    document.body.classList.remove(fontFamilies[fontFamily]);
    document.body.classList.add(fontFamilies[nextFontFamily]);

    setFontFamily(nextFontFamily);
  };

  const toggleLang = () => {
    const nextLang = (lang + 1) % 2;

    setLang(nextLang);

    document.documentElement.lang = languages[nextLang];
  };
  const printDocument = () => window.print();

  return (
    <div className="flex flex-column items-end" style={style}>
      <button className={className} onClick={printDocument} title="Print">
        <IconPrinter />
      </button>
      <button className={className} onClick={toggleLang} title="Language">
        <IconLanguage />
      </button>
      <button className={className} onClick={toggleTheme} title="Theme">
        {lightTheme && <IconSunrise />}
        {!lightTheme && <IconSunset />}
      </button>
      {languages[lang] !== "zh" && (
        <button className={className} onClick={toggleFontFamily} title="Font">
          <strong>{fonts[fontFamily]}</strong>
        </button>
      )}
    </div>
  );
}

export default Settings;
