function Experience({
  companyName,
  children,
  endDate,
  location,
  startDate,
  title,
}) {
  const style = { flexBasis: 200 };

  return (
    <div className="mb-4">
      <div className="flex">
        <div className="text-italic mt-4 pr-4" style={style}>
          {title}
        </div>
        <div>
          <small className="text-italic mr-4">
            {startDate} - {endDate}
          </small>
          <strong className="text-large">
            {companyName}, {location}
          </strong>
          <div className="mb-2">{children}</div>
        </div>
      </div>
    </div>
  );
}

export default Experience;
